.cnt {
  padding: 32px 0px;
}

.cnt_banner {
  width: 100%;
  aspect-ratio: 1320/646;
  background-color: #7366ae;
  border-radius: 12px;
  overflow: hidden;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.circle_cnt {
  height: 30%;
  aspect-ratio: 1/1;
  border-radius: 100%;
  background-color: #ABD372;
  position: absolute;
  bottom: -10%;
  left: -5%;
}

.circle_cnt_1 {
  position: absolute;
  height: 30%;
  aspect-ratio: 1/1;
  background-color: #ABD372;
  border-radius: 100%;
  right: -9%;
  top: -5%;
}

.banner_content {
  height: 66%;
  aspect-ratio: 2/0.8;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.banner_content_img {
  width: 55%;
}

.banner_content_left {
  color: var(--white);
}

.banner_content_left_title {
  font-size: 32px;
  font-weight: 700;
  margin-bottom: 12px;
}

.banner_content_left_item {
  display: flex;
  align-items: center;
  font-size: 22px;
  font-weight: 500;
}

.banner_content_left_item img {
  margin-right: 6px;
}

.banner_content_btn {
  margin-top: 24px;
  font-size: 20px;
  padding: 16px 20px;
  border-radius: 8px;
  text-transform: uppercase;
  background-color: #ABD372;
  font-weight: 700;
}

.section_cnt {
  padding: 32px 0px;
}

.section_title {
  margin-bottom: 20px;
  text-align: center;
  font-size: 35px;
  font-weight: 500;
}

.benefit_list {
  grid-gap: 28px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
.benefit_item{
  background: #F9FFF4;
  border: 1px solid #95BF70;
  padding: 32px 12px;
  border-radius: 12px;
  display: flex;
  align-items: center;
}
.benefit_item_de{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.benefit_item_de img{
  width: 40%;
}
.benefit_item_de span{
  font-size: 16px;
  font-weight: 400;
}
.step_list{
  grid-gap: 28px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
.step_item_cnt{
  padding: 34px 20px;
  border: 1px solid #7366AE;
  background: #F4F2FF;
  border-radius: 12px;
  position: relative;
  overflow: hidden;
}
.step_item_number{
  position: absolute;
  width: 20%;
  aspect-ratio: 1/1;
  border-radius: 100%;
  background-color: #7366AE;
  top: -6%;
  right: -4%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 30px;
  color: var(--white);
  font-weight: 600;
}
.step_item_label{
  font-size: 20px;
  font-weight: 700;
  margin: 28px 0px 16px 0px;
}
.step_item_desc{
  font-size: 16px;
  font-weight: 400;
}
.step_item_desc span{
  font-weight: 600;
  cursor: pointer;
}
.step_item_desc span:hover{
  text-decoration: underline;
}
.footer{
  width: 100%;
  aspect-ratio: 1440/385;
  background-color:#ABD372;
  margin-top: 140px;
}